.tns-outer {
  padding: 0 !important;
}
.tns-outer [hidden] {
  display: none !important;
}
.tns-outer [aria-controls], .tns-outer [data-action] {
  cursor: pointer;
}

.tns-slider {
  -webkit-transition: all 0s;
  -moz-transition: all 0s;
  transition: all 0s;
}
.tns-slider > .tns-item {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.tns-horizontal.tns-subpixel {
  white-space: nowrap;
}
.tns-horizontal.tns-subpixel > .tns-item {
  display: inline-block;
  vertical-align: top;
  white-space: normal;
}
.tns-horizontal.tns-no-subpixel:after {
  content: "";
  display: table;
  clear: both;
}
.tns-horizontal.tns-no-subpixel > .tns-item {
  float: left;
}
.tns-horizontal.tns-carousel.tns-no-subpixel > .tns-item {
  margin-right: -100%;
}

.tns-no-calc {
  position: relative;
  left: 0;
}

.tns-gallery {
  position: relative;
  left: 0;
  min-height: 1px;
}
.tns-gallery > .tns-item {
  position: absolute;
  left: -100%;
  -webkit-transition: transform 0s, opacity 0s;
  -moz-transition: transform 0s, opacity 0s;
  transition: transform 0s, opacity 0s;
}
.tns-gallery > .tns-slide-active {
  position: relative;
  left: auto !important;
}
.tns-gallery > .tns-moving {
  -webkit-transition: all 0.25s;
  -moz-transition: all 0.25s;
  transition: all 0.25s;
}

.tns-autowidth {
  display: inline-block;
}

.tns-lazy-img {
  -webkit-transition: opacity 0.6s;
  -moz-transition: opacity 0.6s;
  transition: opacity 0.6s;
  opacity: 0.6;
}
.tns-lazy-img.tns-complete {
  opacity: 1;
}

.tns-ah {
  -webkit-transition: height 0s;
  -moz-transition: height 0s;
  transition: height 0s;
}

.tns-ovh {
  overflow: hidden;
}

.tns-visually-hidden {
  position: absolute;
  left: -10000em;
}

.tns-transparent {
  opacity: 0;
  visibility: hidden;
}

.tns-fadeIn {
  opacity: 1;
  filter: alpha(opacity=100);
  z-index: 0;
}

.tns-normal, .tns-fadeOut {
  opacity: 0;
  filter: alpha(opacity=0);
  z-index: -1;
}

.tns-vpfix {
  white-space: nowrap;
}
.tns-vpfix > div, .tns-vpfix > li {
  display: inline-block;
}

.tns-t-subp2 {
  margin: 0 auto;
  width: 310px;
  position: relative;
  height: 10px;
  overflow: hidden;
}
.tns-t-ct {
  width: 2333.3333333333%;
  width: -webkit-calc(100% * 70 / 3);
  width: -moz-calc(100% * 70 / 3);
  width: calc(100% * 70 / 3);
  position: absolute;
  right: 0;
}
.tns-t-ct:after {
  content: "";
  display: table;
  clear: both;
}
.tns-t-ct > div {
  width: 1.4285714286%;
  width: -webkit-calc(100% / 70);
  width: -moz-calc(100% / 70);
  width: calc(100% / 70);
  height: 10px;
  float: left;
}

/* Slider */
.slider-wrap {
  position: relative;
  margin: -10px;
}

.slider .slider-card {
  margin: 10px;
}

.tns-ovh {
  margin-bottom: 40px;
}

.tns-controls button {
  position: absolute;
  top: calc(50% - 27px - 32px);
  width: 54px;
  height: 54px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #FFE26C;
  border: none;
  border-radius: 50%;
  transition: all 0.2s ease-in-out;
}
.tns-controls button:hover {
  background-color: #FFCC00;
}
.tns-controls button[data-controls=prev] {
  left: 0;
}
.tns-controls button[data-controls=next] {
  right: 0;
}

.tns-nav {
  text-align: center;
}
.tns-nav button {
  padding: 0;
  width: 6px;
  height: 6px;
  background-color: #D0D9FA;
  border: none;
  border-radius: 50%;
  margin: 2px;
}
.tns-nav button.tns-nav-active {
  background-color: #6165D7;
}

@media (max-width: 575px) {
  .tns-ovh {
    margin-bottom: 20px;
  }

  .tns-controls button {
    position: absolute;
    top: calc(50% - 13.5px - 19px);
    width: 38px;
    height: 38px;
    border-radius: 0;
    z-index: 50;
  }
  .tns-controls button:hover {
    background-color: #FFCC00;
  }
  .tns-controls button svg {
    width: 8px;
  }
  .tns-controls button[data-controls=prev] {
    left: 0;
  }
  .tns-controls button[data-controls=next] {
    right: 0;
  }
}
/* Block */
.breadcrumbs-section {
  padding-top: 70px;
  padding-bottom: 50px;
}
.breadcrumbs-section .breadcrumb {
  padding: 0;
  margin: 0;
  background-color: transparent;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item {
  position: relative;
  font-size: 14px;
  font-weight: 500;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item.active {
  color: #2B478B;
}
.breadcrumbs-section .breadcrumb a {
  color: #767676;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item + .breadcrumb-item {
  padding-left: 30px;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item + .breadcrumb-item::before {
  content: "";
  position: absolute;
  top: calc(50% - 4px);
  left: calc(15px - 4px);
  width: 5px;
  height: 8px;
  background-image: url(../../assets/img/svg/angle-right.svg);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

@media (max-width: 991px) {
  .breadcrumbs-section {
    padding-top: 20px;
  }
}
